.reading-progress-page {
    padding: 30px 20px;
    min-height: calc(100vh - 150px);
}

.reading-progress-container {
    max-width: 1200px;
    margin: 0 auto;
}

.page-title {
    font-size: 2rem;
    margin-bottom: 30px;
    color: #333;
    border-bottom: 2px solid #f0f0f0;
    padding-bottom: 10px;
}

.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 300px;
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top-color: #4285f4;
    animation: spin 1s linear infinite;
    margin-bottom: 15px;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.error-message {
    background-color: #fff0f0;
    color: #e53935;
    padding: 15px;
    border-radius: 4px;
    margin-bottom: 20px;
    border-left: 3px solid #e53935;
}

/* 空进度状态 */
.empty-progress {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 20px;
    text-align: center;
    background-color: #f8f9fa;
    border-radius: 8px;
}

.empty-icon {
    font-size: 48px;
    margin-bottom: 20px;
}

.empty-progress h3 {
    font-size: 1.4rem;
    margin-bottom: 10px;
    color: #333;
}

.empty-progress p {
    color: #666;
    margin-bottom: 25px;
    max-width: 400px;
}

.browse-books-btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 500;
    transition: background-color 0.2s;
}

.browse-books-btn:hover {
    background-color: #3367d6;
}

/* 进度列表 */
.progress-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.progress-item {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

@media (min-width: 768px) {
    .progress-item {
        flex-direction: row;
        align-items: center;
    }
}

/* 书籍信息部分 */
.book-info {
    display: flex;
    gap: 15px;
    flex: 3;
}

.book-cover {
    width: 100px;
    height: 150px;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

.book-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.book-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.book-title {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 5px;
    color: #333;
}

.book-author {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 10px;
}

.progress-date {
    font-size: 0.85rem;
    color: #888;
    margin-bottom: 10px;
}

.view-book-link {
    color: #4285f4;
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 500;
}

.view-book-link:hover {
    text-decoration: underline;
}

/* 进度跟踪部分 */
.progress-tracker {
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.progress-bar-container {
    height: 12px;
    background-color: #f0f0f0;
    border-radius: 6px;
    position: relative;
    overflow: hidden;
}

.progress-bar-fill {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #4285f4;
    border-radius: 6px;
    transition: width 0.5s ease-out;
}

.progress-percentage {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.7rem;
    font-weight: 500;
    color: #333;
    text-shadow: 0 0 2px rgba(255, 255, 255, 0.8);
}

.progress-pages {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #555;
    font-size: 0.9rem;
}

.pages-separator {
    margin: 0 5px;
    color: #888;
}

.progress-actions {
    display: flex;
    justify-content: center;
}

.update-btn {
    background-color: #4285f4;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
}

.update-btn:hover {
    background-color: #3367d6;
}

/* 响应式调整 */
@media (max-width: 576px) {
    .page-title {
        font-size: 1.5rem;
    }

    .progress-item {
        padding: 15px;
    }

    .book-cover {
        width: 80px;
        height: 120px;
    }

    .book-title {
        font-size: 1rem;
    }

    .update-btn {
        width: 100%;
    }
}