.message-board-page {
    padding-bottom: 2rem;
}

.page-header {
    margin-bottom: 2rem;
}

.page-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 0.5rem;
}

.page-description {
    color: #666;
    font-size: 1.1rem;
    max-width: 800px;
    line-height: 1.6;
}

.message-board-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
}

@media (min-width: 992px) {
    .message-board-container {
        grid-template-columns: 1fr 1fr;
    }

    .form-section {
        order: 1;
    }

    .list-section {
        order: 2;
    }
}

@media (max-width: 991px) {
    .form-section {
        order: 2;
    }

    .list-section {
        order: 1;
    }
}