.book-card {
    background-color: var(--cardBackground);
    border-radius: 12px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
    overflow: hidden;
    transition: all 0.3s ease;
    height: 100%;
    position: relative;
    z-index: 1;
    margin-bottom: 0;
    border: 1px solid rgba(0, 0, 0, 0.03);
}

.book-card:hover {
    transform: translateY(-6px);
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.15);
}

.book-link {
    display: block;
    height: 100%;
    text-decoration: none;
    color: inherit;
}

.book-cover {
    position: relative;
    width: 100%;
    height: 240px;
    overflow: hidden;
    background-color: #f0f0f0;
}

.book-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.book-card:hover .book-cover img {
    transform: scale(1.05);
}

.book-category {
    position: absolute;
    top: 10px;
    left: 10px;
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    padding: 5px 10px;
    border-radius: 30px;
    font-size: 0.75rem;
    font-weight: 500;
    backdrop-filter: blur(4px);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    z-index: 2;
}

.book-info {
    padding: 16px;
    display: flex;
    flex-direction: column;
    height: calc(100% - 240px);
    background: linear-gradient(to bottom, var(--cardBackground) 90%, rgba(240, 240, 240, 0.3));
}

.book-title {
    font-size: 1rem;
    margin: 0 0 8px;
    color: var(--textColor);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 1.35;
    font-weight: 600;
    min-height: 2.7em;
}

.book-author {
    font-size: 0.85rem;
    color: #666;
    margin: 0 0 auto;
    opacity: 0.85;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.book-rating {
    margin-top: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 6px 8px;
    border-radius: 6px;
    background-color: rgba(0, 0, 0, 0.02);
    border-top: 1px solid rgba(0, 0, 0, 0.03);
}

.stars {
    display: flex;
    align-items: center;
}

.star {
    color: #ffc107;
    font-size: 0.9rem;
    margin-right: 2px;
}

.star.empty {
    color: #e0e0e0;
}

.star.half {
    position: relative;
    display: inline-block;
    color: #e0e0e0;
}

.star.half:before {
    content: '★';
    position: absolute;
    left: 0;
    width: 50%;
    overflow: hidden;
    color: #ffc107;
}

.rating-value {
    font-size: 0.85rem;
    font-weight: 600;
    color: #666;
    background-color: rgba(255, 193, 7, 0.15);
    padding: 3px 8px;
    border-radius: 20px;
}

/* 添加装饰元素 */
.book-card::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--primaryColor), #6ab7ff);
    z-index: 2;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.4s ease;
}

.book-card:hover::before {
    transform: scaleX(1);
}

@media (max-width: 992px) {
    .book-cover {
        height: 220px;
    }

    .book-info {
        padding: 14px;
        height: calc(100% - 220px);
    }
}

@media (max-width: 768px) {
    .book-cover {
        height: 200px;
    }

    .book-info {
        padding: 12px;
        height: calc(100% - 200px);
    }

    .book-title {
        font-size: 0.95rem;
        min-height: 2.6em;
    }
}

@media (max-width: 576px) {
    .book-cover {
        height: 180px;
    }

    .book-info {
        padding: 10px;
        height: calc(100% - 180px);
    }

    .book-title {
        font-size: 0.9rem;
        min-height: 2.5em;
        margin-bottom: 6px;
    }

    .book-author {
        font-size: 0.8rem;
    }

    .book-rating {
        padding: 5px 7px;
        margin-top: 10px;
    }

    .star {
        font-size: 0.85rem;
    }

    .rating-value {
        font-size: 0.8rem;
        padding: 2px 7px;
    }
}