.settings-page {
    padding: 30px 20px;
    min-height: calc(100vh - 150px);
}

.settings-container {
    max-width: 800px;
    margin: 0 auto;
}

.page-title {
    font-size: 2rem;
    margin-bottom: 30px;
    color: #333;
    border-bottom: 2px solid #f0f0f0;
    padding-bottom: 10px;
}

.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 300px;
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top-color: #4285f4;
    animation: spin 1s linear infinite;
    margin-bottom: 15px;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.success-message {
    background-color: #f0f9eb;
    color: #67c23a;
    padding: 12px 15px;
    border-radius: 4px;
    margin-bottom: 20px;
    border-left: 3px solid #67c23a;
    font-size: 14px;
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* 设置区域 */
.settings-sections {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.settings-section {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

.section-title {
    font-size: 1.2rem;
    padding: 15px 20px;
    margin: 0;
    border-bottom: 1px solid #f0f0f0;
    background-color: #f8f9fa;
    color: #333;
}

/* 设置项目 */
.setting-item {
    display: flex;
    padding: 20px;
    border-bottom: 1px solid #f0f0f0;
}

.setting-item:last-child {
    border-bottom: none;
}

.setting-info {
    flex: 1;
}

.setting-title {
    font-size: 1rem;
    margin: 0 0 5px;
    color: #333;
}

.setting-description {
    font-size: 0.85rem;
    color: #666;
    margin: 0;
}

.setting-control {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

/* 下拉选择框 */
.select-control {
    padding: 8px 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #fff;
    font-size: 14px;
    min-width: 120px;
}

.select-control:focus {
    outline: none;
    border-color: #4285f4;
    box-shadow: 0 0 0 2px rgba(66, 133, 244, 0.2);
}

/* 开关组件 */
.toggle-switch {
    position: relative;
    display: inline-block;
    width: 46px;
    height: 24px;
}

.toggle-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.toggle-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.toggle-slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked+.toggle-slider {
    background-color: #4285f4;
}

input:focus+.toggle-slider {
    box-shadow: 0 0 1px #4285f4;
}

input:checked+.toggle-slider:before {
    transform: translateX(22px);
}

/* 设置阅读目标 */
.setting-goal {
    display: flex;
    gap: 10px;
}

.number-input {
    width: 80px;
    padding: 8px 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    text-align: center;
}

.number-input:focus {
    outline: none;
    border-color: #4285f4;
    box-shadow: 0 0 0 2px rgba(66, 133, 244, 0.2);
}

.save-btn {
    padding: 8px 16px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
}

.save-btn:hover {
    background-color: #3367d6;
}

/* 退出登录按钮 */
.logout-btn {
    padding: 8px 16px;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
}

.logout-btn:hover {
    background-color: #d32f2f;
}

/* 响应式设计 */
@media (max-width: 576px) {
    .setting-item {
        flex-direction: column;
        gap: 15px;
    }

    .setting-control {
        margin-left: 0;
        width: 100%;
        justify-content: flex-start;
    }

    .select-control {
        width: 100%;
    }

    .setting-goal {
        width: 100%;
    }

    .number-input {
        flex: 1;
    }
}