.not-found-page {
    min-height: calc(100vh - 300px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem 0;
}

.not-found-container {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 3rem;
    text-align: center;
    max-width: 500px;
    width: 100%;
}

.not-found-code {
    font-size: 8rem;
    font-weight: 700;
    line-height: 1;
    color: #1890ff;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.not-found-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1rem;
}

.not-found-message {
    color: #666;
    font-size: 1.1rem;
    margin-bottom: 2rem;
}

.not-found-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.action-btn {
    padding: 0.8rem 1.5rem;
    border-radius: 4px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s;
    text-decoration: none;
}

.action-btn.primary {
    background-color: #1890ff;
    color: white;
}

.action-btn.primary:hover {
    background-color: #40a9ff;
}

.action-btn.secondary {
    background-color: #f0f0f0;
    color: #333;
}

.action-btn.secondary:hover {
    background-color: #e0e0e0;
}

@media (max-width: 576px) {
    .not-found-container {
        padding: 2rem;
    }

    .not-found-code {
        font-size: 6rem;
    }

    .not-found-title {
        font-size: 1.5rem;
    }

    .not-found-actions {
        flex-direction: column;
    }
}