.book-detail-page {
    padding: 2rem 0;
    max-width: 1200px;
    margin: 0 auto;
}

.book-header {
    margin-bottom: 20px;
}

.book-header h1 {
    font-size: 26px;
    font-weight: 700;
    color: #494949;
    margin: 0;
    padding: 0 0 15px 0;
}

.book-detail-container {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 30px;
    margin-bottom: 40px;
}

/* 左侧信息栏 */
.book-info-left {
    color: #666;
    font-size: 13px;
    line-height: 1.62;
}

.book-author {
    margin-bottom: 8px;
    color: #333;
}

.book-author a {
    color: #333;
    text-decoration: none;
}

.book-meta {
    margin-bottom: 20px;
}

.meta-item {
    margin-bottom: 6px;
    display: flex;
}

.meta-label {
    color: #666;
    min-width: 60px;
}

.meta-value {
    flex: 1;
}

/* 中间封面栏 */
.book-cover-container {
    text-align: center;
}

.book-cover {
    max-width: 135px;
    margin: 0 auto;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 0;
}

.book-cover img {
    width: 100%;
    height: auto;
    display: block;
}

/* 右侧评分栏 */
.book-rating-container {
    padding-top: 10px;
}

.rating-header {
    font-size: 15px;
    color: #9b9b9b;
    margin-bottom: 8px;
}

.rating-big {
    font-size: 28px;
    color: #494949;
    font-weight: 700;
    margin-right: 10px;
}

.rating-count {
    color: #9b9b9b;
    font-size: 13px;
}

.rating-stars {
    margin: 5px 0 8px;
    height: 15px;
}

.star {
    color: #FFAC2D;
    font-size: 15px;
    margin-right: 1px;
}

.star.empty {
    color: #eee;
}

.rating-distribution {
    margin-top: 15px;
    font-size: 12px;
}

.rating-item {
    display: flex;
    align-items: center;
    margin-bottom: 2px;
}

.rating-level {
    width: 30px;
    text-align: right;
    margin-right: 5px;
}

.rating-bar {
    flex: 1;
    height: 8px;
    background-color: #f0f0f0;
    border-radius: 2px;
    overflow: hidden;
    margin: 0 8px;
}

.rating-bar-inner {
    height: 100%;
    background-color: #FFAC2D;
}

.rating-percent {
    color: #9b9b9b;
    width: 40px;
}

/* 阅读状态按钮 */
.book-actions {
    margin: 25px 0;
    display: flex;
    gap: 8px;
}

.action-btn {
    padding: 8px 12px;
    border-radius: 3px;
    font-size: 13px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
    border: 1px solid #dfdfdf;
    font-weight: normal;
    background-color: #fff;
    color: #333;
}

.action-btn:hover {
    background-color: #f5f5f5;
}

.action-btn.active {
    background-color: #41ac52;
    color: white;
    border-color: #41ac52;
}

.action-icon {
    margin-right: 5px;
    font-size: 13px;
}

/* 操作栏 */
.operation-bar {
    display: flex;
    align-items: center;
    gap: 15px;
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
    margin-bottom: 25px;
}

.operation-item {
    display: flex;
    align-items: center;
    font-size: 13px;
    color: #666;
    cursor: pointer;
}

.operation-icon {
    margin-right: 4px;
    font-size: 15px;
}

/* 内容区域 */
.book-content {
    padding: 0;
    box-shadow: none;
    background: transparent;
}

.content-section {
    margin-bottom: 30px;
}

.content-title {
    font-size: 16px;
    color: #494949;
    margin: 0 0 12px 0;
    padding: 0 0 5px 0;
    font-weight: 500;
    border-bottom: 1px solid #eee;
}

.book-description {
    font-size: 13px;
    line-height: 1.62;
    color: #333;
}

/* 响应式布局 */
@media (max-width: 1024px) {
    .book-detail-container {
        grid-template-columns: 2fr 1fr;
    }

    .book-rating-container {
        grid-row: 2;
        grid-column: 1 / span 2;
    }
}

@media (max-width: 768px) {
    .book-detail-container {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    .book-cover-container {
        margin: 0 auto;
        max-width: 135px;
    }

    .book-rating-container {
        grid-row: auto;
        grid-column: auto;
    }

    .operation-bar {
        flex-wrap: wrap;
    }
}

/* 加载状态 */
.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    text-align: center;
}

.loading-spinner {
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #41ac52;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 20px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

/* 错误状态 */
.error-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    text-align: center;
    color: #666;
}

.error-state h2 {
    color: #333;
    margin-bottom: 10px;
}

.error-state .btn {
    margin-top: 20px;
    padding: 8px 16px;
    background-color: #41ac52;
    color: white;
    border: none;
    border-radius: 3px;
    text-decoration: none;
    font-size: 14px;
}

.error-state .btn:hover {
    background-color: #3da04d;
}

/* 阅读进度样式 */
.reading-progress-container {
    margin: 15px 0 25px;
    background-color: #f9f9f9;
    padding: 15px;
    border-radius: 4px;
    border: 1px solid #eee;
}

.reading-progress-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
    font-size: 14px;
    color: #494949;
}

.update-progress-btn {
    border: none;
    background: none;
    color: #0c88e8;
    font-size: 13px;
    cursor: pointer;
    padding: 0;
}

.progress-bar-container {
    height: 8px;
    background-color: #f0f0f0;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 8px;
}

.progress-bar-fill {
    height: 100%;
    background-color: #41ac52;
    transition: width 0.3s ease;
}

.progress-info {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    color: #666;
}

.reading-form {
    margin-top: 15px;
    border-top: 1px solid #eee;
    padding-top: 15px;
}

.reading-form .form-group {
    margin-bottom: 12px;
    position: relative;
}

.reading-form label {
    display: block;
    margin-bottom: 8px;
    font-size: 13px;
    color: #333;
}

.reading-form input {
    width: 80px;
    padding: 6px 8px;
    border: 1px solid #ddd;
    border-radius: 3px;
    font-size: 14px;
}

.total-pages {
    font-size: 14px;
    color: #666;
    margin-left: 8px;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.save-btn,
.cancel-btn,
.submit-btn {
    padding: 6px 12px;
    border-radius: 3px;
    font-size: 13px;
    cursor: pointer;
    border: none;
}

.save-btn,
.submit-btn {
    background-color: #41ac52;
    color: white;
}

.save-btn:hover,
.submit-btn:hover {
    background-color: #3da04d;
}

.cancel-btn {
    background-color: #f0f0f0;
    color: #666;
}

.cancel-btn:hover {
    background-color: #e0e0e0;
}

/* 书评样式 */
.review-form-container {
    background-color: #f9f9f9;
    padding: 15px;
    border-radius: 4px;
    margin: 15px 0 25px;
    border: 1px solid #eee;
}

.review-form-container h3 {
    font-size: 15px;
    margin: 0 0 15px;
    color: #494949;
}

.rating-selector {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
}

.rating-star {
    font-size: 24px;
    color: #eee;
    cursor: pointer;
    transition: all 0.2s;
}

.rating-star.active {
    color: #FFAC2D;
}

.rating-star:hover {
    transform: scale(1.1);
}

.rating-text {
    margin-left: 10px;
    font-size: 14px;
    color: #666;
}

.review-form-container textarea {
    width: 100%;
    min-height: 120px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;
    resize: vertical;
    font-size: 14px;
    margin-bottom: 15px;
}

.review-form-container textarea:focus {
    border-color: #41ac52;
    outline: none;
}

.user-rating-prompt {
    margin-top: 20px;
    padding-top: 15px;
    border-top: 1px solid #eee;
}

.user-rating-prompt p {
    font-size: 14px;
    color: #494949;
    margin-bottom: 10px;
}

.rate-btn {
    margin-top: 10px;
    padding: 6px 12px;
    background-color: #41ac52;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 13px;
}

.rate-btn:disabled {
    background-color: #ddd;
    cursor: not-allowed;
}

.rate-btn:hover:not(:disabled) {
    background-color: #3da04d;
}

/* 操作栏增强 */
.operation-item {
    transition: all 0.2s;
}

.operation-item:hover {
    color: #41ac52;
}

.operation-item.active {
    color: #41ac52;
}

/* 标签样式 */
.book-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.book-tag {
    display: inline-block;
    padding: 5px 10px;
    background-color: #f5f5f5;
    color: #666;
    border-radius: 3px;
    font-size: 13px;
    transition: all 0.2s;
}

.book-tag:hover {
    background-color: #e7e7e7;
    cursor: pointer;
}

/* 评论区样式 */
.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.write-review-btn {
    padding: 5px 12px;
    background-color: #41ac52;
    color: white;
    border: none;
    border-radius: 3px;
    font-size: 13px;
    cursor: pointer;
}

.write-review-btn:hover {
    background-color: #3da04d;
}

.comments-list {
    margin-top: 20px;
}

.comment-item {
    border-bottom: 1px solid #f0f0f0;
    padding: 15px 0;
}

.comment-item:first-child {
    padding-top: 0;
}

.comment-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    flex-wrap: wrap;
}

.comment-user {
    display: flex;
    align-items: center;
}

.comment-avatar {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    margin-right: 8px;
}

.comment-username {
    font-weight: 500;
    color: #494949;
    font-size: 14px;
}

.comment-rating {
    display: flex;
    align-items: center;
    margin: 0 12px;
}

.comment-star {
    color: #FFAC2D;
    font-size: 12px;
    margin-right: 1px;
}

.comment-date {
    color: #9b9b9b;
    font-size: 12px;
}

.comment-content {
    font-size: 14px;
    line-height: 1.6;
    color: #333;
    margin-bottom: 12px;
}

.comment-footer {
    display: flex;
    justify-content: flex-end;
}

.comment-like {
    background: none;
    border: none;
    color: #9b9b9b;
    font-size: 12px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 4px;
}

.comment-like:hover {
    color: #e57373;
}

.no-comments {
    text-align: center;
    color: #9b9b9b;
    font-size: 14px;
    padding: 20px 0;
}

/* 响应式调整 */
@media (max-width: 768px) {
    .comment-header {
        flex-direction: column;
        align-items: flex-start;
    }

    .comment-rating {
        margin: 8px 0;
    }

    .reading-form .form-group {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    .reading-form label {
        margin-right: 10px;
        margin-bottom: 0;
    }
}