.home-page {
    padding: 2rem 1.5rem 3rem;
    max-width: 1400px;
    margin: 0 auto;
}

.hero-section {
    margin-bottom: 4.5rem;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
}

.section {
    margin-bottom: 5rem;
}

.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2.5rem;
    position: relative;
}

.section-title {
    font-size: 2rem;
    margin: 0;
    color: var(--textColor);
    border-left: 5px solid var(--primaryColor);
    padding-left: 20px;
    font-weight: 700;
    position: relative;
}

.section-title::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 20px;
    width: 60px;
    height: 3px;
    background-color: var(--primaryColor);
}

.section-link {
    color: var(--primaryColor);
    font-weight: 500;
    transition: all 0.3s;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 1.05rem;
}

.section-link:hover {
    color: #fff;
    background-color: var(--primaryColor);
    text-decoration: none;
    box-shadow: 0 5px 15px rgba(66, 133, 244, 0.3);
    transform: translateY(-3px);
}

.book-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 25px;
    margin: 20px 0;
}

/* 功能介绍部分 */
.features-section {
    text-align: center;
    padding: 4rem 2rem;
    background-color: rgba(66, 133, 244, 0.05);
    border-radius: 16px;
    margin: 5rem 0;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.04);
}

.features-section .section-title {
    margin: 0 auto 4rem;
    display: inline-block;
    border-left: none;
    position: relative;
}

.features-section .section-title::before {
    content: '';
    position: absolute;
    top: 50%;
    left: -80px;
    width: 50px;
    height: 3px;
    background-color: var(--primaryColor);
}

.features-section .section-title::after {
    width: 50px;
    left: auto;
    right: -80px;
    bottom: auto;
    top: 50%;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    gap: 3rem;
    margin-top: 3rem;
}

.feature-card {
    background-color: white;
    padding: 3rem 2rem;
    border-radius: 16px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.08);
    transition: all 0.4s ease;
    position: relative;
    z-index: 1;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.03);
}

.feature-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--primaryColor), #6ab7ff);
    z-index: 2;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.4s ease;
}

.feature-card:hover {
    transform: translateY(-15px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.12);
}

.feature-card:hover::before {
    transform: scaleX(1);
}

.feature-icon {
    font-size: 3.2rem;
    margin-bottom: 2rem;
    position: relative;
    display: inline-block;
}

.feature-icon::after {
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: rgba(66, 133, 244, 0.15);
    border-radius: 50%;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease;
}

.feature-card:hover .feature-icon::after {
    width: 80px;
    height: 80px;
}

.feature-card h3 {
    font-size: 1.5rem;
    margin-bottom: 1.2rem;
    color: var(--textColor);
    font-weight: 600;
}

.feature-card p {
    color: #666;
    margin: 0;
    line-height: 1.7;
    font-size: 1.05rem;
}

/* 号召性用语部分 */
.cta-section {
    background: linear-gradient(135deg, var(--primaryColor) 0%, #0050b3 100%);
    border-radius: 16px;
    padding: 5rem 3rem;
    text-align: center;
    color: white;
    position: relative;
    overflow: hidden;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.cta-section::before {
    content: '';
    position: absolute;
    top: -80px;
    right: -80px;
    width: 200px;
    height: 200px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
}

.cta-section::after {
    content: '';
    position: absolute;
    bottom: -90px;
    left: -90px;
    width: 250px;
    height: 250px;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 50%;
}

.cta-content {
    position: relative;
    z-index: 2;
}

.cta-content h2 {
    font-size: 2.6rem;
    margin-bottom: 1.5rem;
    font-weight: 700;
}

.cta-content p {
    font-size: 1.3rem;
    margin-bottom: 3rem;
    opacity: 0.9;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.7;
}

.cta-button {
    display: inline-block;
    background-color: white;
    color: var(--primaryColor);
    padding: 1.2rem 3rem;
    border-radius: 50px;
    font-weight: 600;
    font-size: 1.15rem;
    transition: all 0.4s;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.cta-button:hover {
    background-color: #f8f8f8;
    transform: translateY(-7px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}

@media (max-width: 992px) {
    .book-grid {
        grid-template-columns: repeat(auto-fill, minmax(170px, 1fr));
        gap: 20px;
    }

    .features-grid {
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
        gap: 2.5rem;
    }

    .cta-content h2 {
        font-size: 2.2rem;
    }
}

@media (max-width: 768px) {
    .home-page {
        padding: 1.5rem 1.2rem 2.5rem;
    }

    .book-grid {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        gap: 16px;
    }

    .features-grid {
        grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
        gap: 2rem;
    }

    .features-section .section-title::before,
    .features-section .section-title::after {
        display: none;
    }

    .features-section {
        padding: 3rem 1.5rem;
    }

    .cta-section {
        padding: 4rem 2rem;
    }

    .cta-content h2 {
        font-size: 2rem;
    }

    .cta-content p {
        font-size: 1.1rem;
    }

    .section-title {
        font-size: 1.7rem;
    }
}

@media (max-width: 576px) {
    .cta-button {
        width: 100%;
        padding: 1rem;
    }

    .feature-card {
        padding: 2rem 1.5rem;
    }

    .book-grid {
        grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
        gap: 14px;
    }

    .hero-section {
        margin-bottom: 3rem;
    }

    .section {
        margin-bottom: 3.5rem;
    }

    .section-header {
        margin-bottom: 1.8rem;
    }

    .section-title {
        font-size: 1.5rem;
    }
}