.collections-page {
    padding: 30px 20px;
    min-height: calc(100vh - 150px);
}

.collections-container {
    max-width: 1200px;
    margin: 0 auto;
}

.collections-title {
    font-size: 2rem;
    margin-bottom: 30px;
    color: #333;
    border-bottom: 2px solid #f0f0f0;
    padding-bottom: 10px;
}

.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 300px;
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top-color: #4285f4;
    animation: spin 1s linear infinite;
    margin-bottom: 15px;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.error-message {
    background-color: #fff0f0;
    color: #e53935;
    padding: 15px;
    border-radius: 4px;
    margin-bottom: 20px;
    border-left: 3px solid #e53935;
}

/* 空收藏状态 */
.empty-collections {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 20px;
    text-align: center;
    background-color: #f8f9fa;
    border-radius: 8px;
}

.empty-icon {
    font-size: 48px;
    margin-bottom: 20px;
}

.empty-collections h3 {
    font-size: 1.4rem;
    margin-bottom: 10px;
    color: #333;
}

.empty-collections p {
    color: #666;
    margin-bottom: 25px;
    max-width: 400px;
}

.browse-books-btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 500;
    transition: background-color 0.2s;
}

.browse-books-btn:hover {
    background-color: #3367d6;
}

/* 收藏书籍网格 */
.collections-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 25px;
}

.collection-item {
    text-decoration: none;
    color: inherit;
    transition: transform 0.2s;
    position: relative;
}

.collection-item:hover {
    transform: translateY(-5px);
}

.book-cover {
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);
    margin-bottom: 10px;
    height: 280px;
}

.book-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.remove-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    color: #e53935;
    border: none;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.2s;
}

.collection-item:hover .remove-btn {
    opacity: 1;
}

.remove-btn:hover {
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.icon-trash:before {
    content: "🗑️";
}

.book-info {
    padding: 5px;
}

.book-title {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 5px;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.book-author {
    font-size: 14px;
    color: #666;
    margin-bottom: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.book-rating {
    display: flex;
    align-items: center;
}

.rating-stars {
    display: flex;
    margin-right: 8px;
}

.star {
    font-size: 14px;
    color: #ddd;
}

.star.filled {
    color: #ffc107;
}

.rating-value {
    font-size: 14px;
    color: #666;
    font-weight: 500;
}

/* 响应式调整 */
@media (max-width: 768px) {
    .collections-grid {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        gap: 20px;
    }

    .book-cover {
        height: 220px;
    }
}

@media (max-width: 576px) {
    .collections-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    .book-cover {
        height: 200px;
    }

    .collections-title {
        font-size: 1.5rem;
    }
}