.book-analysis-page {
    padding-bottom: 2rem;
}

.page-header {
    margin-bottom: 2rem;
}

.page-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 0.5rem;
}

.page-description {
    color: #666;
    font-size: 1.1rem;
    line-height: 1.6;
    max-width: 800px;
}

.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 2rem;
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(24, 144, 255, 0.2);
    border-radius: 50%;
    border-top: 4px solid #1890ff;
    animation: spin 1s linear infinite;
    margin-bottom: 1rem;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loading-state p {
    color: #666;
    font-size: 1rem;
}

.charts-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
}

.chart-card {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 1rem;
    overflow: hidden;
}

/* 响应式布局 */
@media (min-width: 992px) {
    .charts-container {
        grid-template-columns: 1fr 1fr;
    }
}