.book-recommendation-page {
    padding: 2rem 0 3rem;
    max-width: 1400px;
    margin: 0 auto;
}

.page-header {
    margin-bottom: 2.5rem;
    padding: 0 1.5rem;
}

.page-title {
    font-size: 2.2rem;
    color: #333;
    margin-bottom: 0.8rem;
    font-weight: bold;
    position: relative;
    display: inline-block;
}

.page-title:after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 60px;
    height: 4px;
    background: linear-gradient(90deg, var(--primaryColor), #6ab7ff);
    border-radius: 2px;
}

.page-description {
    color: #666;
    font-size: 1.15rem;
    line-height: 1.6;
    max-width: 800px;
}

.filters-section {
    background-color: white;
    border-radius: 12px;
    padding: 2rem;
    margin: 0 1.5rem 2.5rem;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.07);
}

.search-box {
    display: flex;
    margin-bottom: 2rem;
}

.search-box input {
    flex: 1;
    padding: 1rem 1.2rem;
    border: 1px solid #d9d9d9;
    border-right: none;
    border-radius: 6px 0 0 6px;
    font-size: 1rem;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.search-box input:focus {
    outline: none;
    border-color: #1890ff;
    box-shadow: 0 0 0 3px rgba(24, 144, 255, 0.1);
}

.search-btn {
    background-color: #1890ff;
    color: white;
    border: none;
    padding: 0 2rem;
    border-radius: 0 6px 6px 0;
    cursor: pointer;
    transition: background-color 0.3s;
    font-weight: 500;
}

.search-btn:hover {
    background-color: #40a9ff;
}

.filter-controls {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.category-filter,
.sort-filter {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 0.8rem;
}

.filter-label {
    font-weight: 600;
    color: #333;
    margin-right: 0.5rem;
    font-size: 1.05rem;
}

.category-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
}

.category-btn {
    background-color: #f0f0f0;
    border: none;
    padding: 0.6rem 1.2rem;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s;
    font-size: 0.95rem;
}

.category-btn:hover {
    background-color: #e0e0e0;
    transform: translateY(-2px);
}

.category-btn.active {
    background-color: #1890ff;
    color: white;
    box-shadow: 0 5px 15px rgba(24, 144, 255, 0.3);
}

.sort-select {
    padding: 0.7rem 1.2rem;
    border: 1px solid #d9d9d9;
    border-radius: 6px;
    background-color: white;
    cursor: pointer;
    font-size: 0.95rem;
    min-width: 150px;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.sort-select:focus {
    outline: none;
    border-color: #1890ff;
    box-shadow: 0 0 0 3px rgba(24, 144, 255, 0.1);
}

.books-container {
    margin: 0 1.5rem;
}

.book-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    gap: 30px;
}

.no-results {
    text-align: center;
    padding: 4rem 2rem;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.07);
}

.no-results p {
    color: #666;
    font-size: 1.15rem;
}

@media (min-width: 768px) {
    .filter-controls {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .book-grid {
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    }
}

@media (max-width: 992px) {
    .page-title {
        font-size: 2rem;
    }

    .filters-section {
        padding: 1.5rem;
    }

    .book-grid {
        gap: 25px;
    }
}

@media (max-width: 576px) {
    .page-title {
        font-size: 1.8rem;
    }

    .search-box {
        flex-direction: column;
    }

    .search-box input {
        border-right: 1px solid #d9d9d9;
        border-radius: 6px;
        margin-bottom: 0.8rem;
    }

    .search-btn {
        border-radius: 6px;
        padding: 0.8rem;
    }

    .book-grid {
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
        gap: 20px;
    }

    .filters-section {
        padding: 1.2rem;
        margin: 0 1rem 2rem;
    }
}